.closer {
  position: absolute;
  top: 30px;
  right: 20px;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border: none;
}

.closer:before,
.closer:after {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  border-bottom: 2px solid #fff;
}

.closer:before {
  transform: rotate(45deg);
}

.closer:after {
  transform: rotate(-45deg);
}

.closer:hover:before,
.closer:hover:after,
.closer:focus:before,
.closer:focus:after {
  border-bottom-color: #97c89a;
  outline: none;
  transition: border-bottom-color 0.3s ease-in;
}

